import { Component, Input, OnInit, SimpleChange } from '@angular/core';
import { BcSidebarBehavior } from '@bancolombia/design-system-behaviors';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';


@Component({
  selector: 'bc-sidebar',
  templateUrl: './bc-sidebar.component.html',
})
/**
 * Componente Sidebar
 *
 * @export
 * @class BcSidebarComponent
 * @implements {AfterViewInit}
 */
export class BcSidebarComponent implements OnInit {
  
  bcSidebarBehavior;

  constructor(private bcHelperService: BcHelperService) {
    this.bcSidebarBehavior = new BcSidebarBehavior();
  }
  /**
   * Esta entrada permite indicar un id Ãºnico al componente dentro del DOM.
   *
   * @type {string}
   * @memberof BcSidebarComponent
   */
  @Input() id: string = this.bcHelperService.getId(this);

/**
   * Entrada que permite indicar el tipo de Sidebar.
   *
   * @type {string}
   * @memberof BcSidebarComponent
   */
 @Input() tipoSidebar: 'fixed' | 'collapsible' | 'states' | 'hide' = 'fixed';

  /**
  * Indica el aria-label para el comportamiento del componente.
  */

  @Input('aria-label') ariaLabel: string;

  /**
   * Abre el offcanvas.
   *
   * @memberof BcOffCanvasComponent
   */
   openSidebarHide() {
    this.bcSidebarBehavior.configOpenButtonEventHide();
  }
   
  /**
   *
   *
   * @type {Array<any>}
   * @memberof BcSidebarComponent
   */
   @Input() itemsli: Array<any> = [];

    bcSidebarComponent: BcSidebarComponent

  ngOnInit(): void {
      
  }

 ngAfterViewInit(){
  this.bcSidebarBehavior.setId(this.id);
  this.bcSidebarBehavior.setUp(); 
 }

 ngOnChanges(changes: SimpleChange){
  if(changes.currentValue!=changes.previousValue){
    this.tipoSidebar = changes.currentValue;
  }
 }

 
}
